@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap");
html {
  font-size: 0.875rem;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  background-color: #0c122c;
  color: #dddbff;
  margin: 0;
  padding: 0;
}

.body {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
@media (max-width: 39.9375em) {
  .body {
    background-image: url(/app/asset/images/bg-mobile.png);
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -4;
    padding-block: 10rem;
    padding-inline: 1rem;
  }
}
@media (min-width: 40em) {
  .body {
    padding-block: 0;
    padding-inline: 0;
  }
  .body::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    min-height: 50%;
    background-image: url(/app/asset/images/bg-desktop.png);
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -4;
    padding-block: 0;
    padding-inline: 0;
  }
}

.content {
  display: grid;
  grid-template-areas: "menu" "report";
  gap: 1.05rem;
  justify-content: center;
  align-items: center;
  padding: 0;
  max-width: 50.625rem;
  margin: auto 1rem;
  list-style: none;
}
@media (min-width: 40em) {
  .content {
    grid-template-areas: "menu report";
    gap: 2rem;
    margin: 0 auto;
    padding: 0;
  }
}

@media (min-width: 40em) {
  .footer {
    position: absolute;
    bottom: 6.25rem;
  }
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.content__card-fylo {
  margin: 0;
  padding: 2.75rem;
  background-color: #1d2c67;
  border-radius: 10px 100px 10px 10px;
}
@media (min-width: 40em) {
  .content__card-fylo {
    width: 20.1875rem;
  }
}
.content__card-fylo .card-fylo__logo {
  margin-bottom: 1.9rem;
}
.content__card-fylo .card-fylo__menu {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  gap: 0.75rem;
  padding: 0;
  margin: 0;
}
.menu__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.4375rem;
  height: 3.4375rem;
  background-color: #0c122c;
  border-radius: 10px;
  transition: all 0.2s;
}
.menu__icon:hover {
  cursor: pointer;
  background-color: #192148;
}

.content__card-report {
  position: relative;
  margin: 0;
  padding: 2rem;
  background-color: #1d2c67;
  border-radius: 10px;
  text-align: center;
}
@media (min-width: 40em) {
  .content__card-report {
    align-self: flex-end;
    text-align: left;
    width: 31.25rem;
  }
}
.content__card-report .card-report__text {
  font-size: 0.9375rem;
  margin-bottom: 1rem;
}
.content__card-report .card-report__text span {
  font-weight: 700;
}
.content__card-report .card-report__bg {
  width: 100%;
  height: 1.25rem;
  background-color: #0c122c;
  border-radius: 50px;
  margin-bottom: 0.4375rem;
  padding: 0.234375rem;
}
.content__card-report .card-report__bg .bg__info-bar {
  width: 81.5%;
  height: 100%;
  background: linear-gradient(to right, #ffa399, #ff4d97);
  border-radius: 5px;
  padding: 0.109375rem;
}
.content__card-report .card-report__bg .bg__info-bar .info-bar__dot {
  width: 0.5rem;
  height: 0.5rem;
  background-color: #fff;
  border-radius: 50%;
  margin-left: auto;
}
.content__card-report .card-report__amt {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.content__card-report .card-report__amt > span {
  font-size: 0.8125rem;
  font-weight: 700;
  text-transform: uppercase;
}

.card-report__popup {
  position: absolute;
  top: 80%;
  left: 26%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
  background-color: #fff;
  border-radius: 10px;
  margin: 0;
  padding-inline: 1rem;
}
.card-report__popup .popup__number {
  font-size: 2.5rem;
  font-weight: 700;
  color: black;
  margin-bottom: 0.3125rem;
}
.card-report__popup .popup__text {
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #848794;
}

@media (min-width: 40em) {
  .card-report__popup {
    top: -26%;
    left: 62%;
  }

  .popup__tail {
    position: absolute;
    right: 0;
    bottom: -22%;
    width: 1.5rem;
    height: 1.5rem;
    background-color: white;
    clip-path: polygon(0 0, 100% 0, 100% 100%);
  }
}
.footer__attribution {
  font-size: 1rem;
  text-align: center;
}
a {
  color: #3e52a3;
  transition: all 250ms;
}
a:hover {
  color: #848794;
}

@media (max-width: 39.9375em) {
  .footer__attribution {
    display: none;
  }
}